import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../../auth/auth.service';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Router } from '@angular/router';

import { WaterAvailable, Pump } from '../../../Models/telemetry.class';


@Component({
  selector: 'app-telemetry',
  templateUrl: './telemetry.component.html',
  styleUrls: ['./telemetry.component.scss']
})
export class TelemetryComponent implements OnInit {

  tokenJSON = JSON.parse(localStorage.getItem('currentUser'));
  token = this.tokenJSON.token;
  public customerID = localStorage.getItem('idCustomer');
  WaterLevel: WaterAvailable[];
  Pump1: Pump[];
  Pump2: Pump[];

  errorMessage;
  deviceIDController = '483f7ed0-d45c-11e7-a2cc-0b6a5e10d27e';
  deviceIDPump1 = '53751030-d45c-11e7-a2cc-0b6a5e10d27e';
  deviceIDPump2 = '604035b0-d45c-11e7-a2cc-0b6a5e10d27e';


  constructor(private authService: AuthService,
    private http: Http,
    private router: Router) { }

  ngOnInit() {
    this.getAvailableWatter();
    this.getStatusPump1();
    this.getStatusPump1();

  }

  getAvailableWatter () {
    this.authService.getTelemetry(this.token, this.deviceIDController, 'availableWater')
    .subscribe((data) => {
      this.WaterLevel = data;
      console.log('Water Level: ', this.WaterLevel);
    }, (error) => {this.errorMessage = error; }
  );
  }

  getStatusPump1 () {
    this.authService.getTelemetry(this.token, this.deviceIDPump1, 'actuation,status')
    .subscribe((data) => {
      this.Pump1 = data;

      console.log('Dados Bomba 1: ', this.Pump1);
    }, (error) => {this.errorMessage = error; }
  );
  }
  getStatusPump2 () {
    this.authService.getTelemetry(this.token, this.deviceIDPump2, 'actuation,status')
    .subscribe((data) => {
      this.Pump1 = data;

      console.log('Dados Bomba 2: ', this.Pump2);
    }, (error) => {this.errorMessage = error; }
  );
  }

}



