import { Component, OnInit } from '@angular/core';
import { routerTransition } from '../../router.animations';
import { Router } from '@angular/router';
import {HttpModule, Http, Response, Headers, RequestOptions } from '@angular/http';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { ApiService } from '../../shared/services/api.service';
import { Customers } from '../../Models/customers.class';
import { AuthService } from '../../auth/auth.service';
import { DataService } from '../../shared/services/data-service.service';
import { CustomersDataObject } from '../../Models/customers.class';




@Component({
  selector: 'app-customer',
  templateUrl: './customer.component.html',
  styleUrls: ['./customer.component.scss'],
  animations: [routerTransition()]
})
export class CustomerComponent implements OnInit {

  loading = false;
  errorMessage;
  tbToken: any = null;
  customerData: CustomersDataObject[];
  customerId = '';



  constructor (
    private http: Http,
    private apiService: ApiService,
    private router: Router,
    private _dataService: DataService) { }

  ngOnInit() {
    this.loading = true;
    if (JSON.parse(localStorage.getItem('currentUser')) === null) {
    this.apiService.tbLogin()
    .subscribe(response => {
      if (response === true) {   // login successful
        const tbUser = JSON.parse(localStorage.getItem('currentUser'));
        this.tbToken = tbUser.token;
        console.log('the tbToken is : ===>', this.tbToken);
    }
    },
      (error) => {this.errorMessage = error; this.loading = false; },
      () => {this.loading = false; this.getCustomers(); }
    );
      } else {
        this.getCustomers();
      }

  }

  getCustomers() {
    this.loading = true;
    this.errorMessage = '';
    const tbUser = JSON.parse(localStorage.getItem('currentUser'));
    this.tbToken = tbUser.token;
    this.apiService.getCustomers(this.tbToken)
        .subscribe((data) => {
          this.customerData = data;
          console.log('Customers list:' , this.customerData);
        },
        (error) => {this.errorMessage = error; this.loading = false; },
        () => {this.loading = false;
        }
      );
  }
  storeCustomerId(id) {
    this._dataService.customerId = id;
    this.customerId = id;
    localStorage.setItem('customerId', id);
  }
}



