import { Component, OnInit} from '@angular/core';
import { AuthService } from '../../../auth/auth.service';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Router } from '@angular/router';
import { ApiService } from '../../../shared/services/api.service';
import { DataService } from '../../../shared/services/data-service.service';

import { AssetsDataObject } from '../../../Models/assets.class';
import { Customers } from '../../../Models/customers.class';



@Component({
  selector: 'app-assets',
  templateUrl: './assets.component.html',
  styleUrls: ['./assets.component.scss']
})
export class AssetsComponent implements OnInit {
  loading = false;
  errorMessage;
  tbToken: any = null;
  assetsData: AssetsDataObject[];
  customerId = localStorage.getItem('customerId');
  typeAsset = 'Campus';


  constructor (
    private http: Http,
    private apiService: ApiService,
    private router: Router,
    private _dataService: DataService) {
    }


  ngOnInit() {
    console.log('Customer ID: ', this.customerId);
    console.log( 'Lista de Assets: ', this.getAssets());

  }

  getAssets() {
    this.loading = true;
    this.errorMessage = '';
    const tbUser = JSON.parse(localStorage.getItem('currentUser'));
    this.tbToken = tbUser.token;
    this.apiService.getAssets1(this.tbToken, this.customerId, this.typeAsset)
        .subscribe((data) => {
          this.assetsData = data;
          console.log('Assets list:' , this.assetsData);
        },
        (error) => {this.errorMessage = error; this.loading = false; },
        () => {this.loading = false;
        }
      );
  }
  storeAssetId(id) {
    this._dataService.assetId = id;
    localStorage.setItem('assetId', id);

  }
  return() {
    this.router.navigate(["/customer"]);

  }


}



