import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router, ActivatedRoute } from '@angular/router';
import { platformBrowserDynamic} from '@angular/platform-browser-dynamic';
import { ReactiveFormsModule, FormControl, FormsModule} from '@angular/forms';
import { RelationDataObject } from '../../../Models/relation.class';

import { ApiService } from '../../../shared/services/api.service';
import { DataService } from '../../../shared/services/data-service.service';
import { HttpModule, Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable} from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/operator/switchMap';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';

@Component({
  selector: 'app-devices',
  templateUrl: './devices.component.html',
  styleUrls: ['./devices.component.scss']
})
export class DevicesComponent implements OnInit {
  data: any = null;
  tbToken: any = null;
  loading = false;
  tbUser: string;
  qParams;
  devicesData: RelationDataObject[];
  assetId: string;
  errorMessage;
  assetObject;

  constructor(
    private http: Http,
    private apiService: ApiService,
    private router: Router,
    private route: ActivatedRoute,
    private _dataService: DataService
  ) { }

  ngOnInit() {
    this.assetId = localStorage.getItem('assetId');
    console.log('asset id is : ==========================>', this.assetId);
    this.loading = true;
    this.errorMessage = '';
    this.getDevicesbyAsset();
  }

  getDevicesbyAsset() {
    this.loading = true;
    this.errorMessage = '';
    const tbUser = JSON.parse(localStorage.getItem('currentUser'));
    this.tbToken = tbUser.token;
    this.apiService.getAssetDevices(this.tbToken, this.assetId)
        .subscribe((data) => {
          this.devicesData = data;
          console.log('Devices list:' , this.devicesData);
        },
        (error) => {this.errorMessage = error; this.loading = false; },
        () => {this.loading = false;
        }
      );
  }
  return() {
    this.router.navigate(["/customer"]);

  }

}



